$darken: 7%;
$gray: #ccc;
$accent: #B09C6D;
$accent-blue: blue;
.btn {
    display: inline-block;
    color: #fff;
    background: $accent;
    text-align: center;
    border-radius: 4px;
    padding: 10px 51px 12px 30px;

    letter-spacing: .5px;
    transition: background .3s;
    font-size: 16px;
    //+light
    font-weight: normal;
    line-height: 1.4;
    white-space: nowrap;
    position: relative;

    padding-top: 0;
    padding-bottom: 0;
    //height: 44px
    //line-height: 44px - 1px
    @include gpu;
    @include xs {
        width: auto;
        display: block; }
    &:hover {
        background: rgba($accent, 0.9);
        .icon {
            transform: translateX(3px);
            transform: translate(3px,-50%); } }
    .icon {
        fill: #fff;
        display: inline-block;
        vertical-align: middle;
        font-size: 16px;
        position: relative;
        top: -1px;
        transition: transform .3s linear;

        position: absolute;
        right: 30px;
        top: 50%;
        transform: translate(0,-50%);
        @include xs {
 } } }            //display: none
.btn_lg {
    font-size: 20px;
    position: relative;
    height: 48px;
    line-height: 48px;
    padding-right: 51px;
    .icon {
        position: absolute;
        top: 50%;
        right: 20px;
        margin: auto;
        font-size: 20px; } }
