.btn_blue {
    background: $accent_blue;
    &:hover {
        background: rgba($accent_blue, 0.9); } }
.btn_white {
    background: #fff;
    color: #333;
    border: 1px solid  #eaeaea;
    .icon {
        fill: #333; }
    .filter__arrowdown {
        content: '';
        position: absolute;
        top: 50%;
        right: 20px;
        margin-top: -2px;
        @include arr(8,5,#000,b);
        pointer-events: none; }
    @include hover {
        background: #fafafa; }
    &:active,&:hover {
        background: #fafafa; } }
.btn_afterlist {
    margin-left: 28px;
    @include xs {
        margin-left: 0; } }
.btn_big {
    // padding-top: 12px
    // padding-bottom: 14px
    height: 48px;
    line-height: 48px; }

.btn-close {
    background: transparent;
    position: absolute;
    top: 0;
    right: 0;
    height: 50px;
    width: 60px;
    text-align: center;
    display: block;
    @include vertical;
    cursor: pointer;
    .icon {
        font-size: 23px;
        vertical-align: middle; } }
